<template>
      <div style="width: 100%;height: 100%">
        <el-descriptions
          class="margin-top"
          title="用户信息"
          :column="3"
          :size="size"
          border
        >
          <el-descriptions-item>
            <template #label>
              <div class="cell-item">
                id
              </div>
            </template>
            {{selectedUser.id}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template #label>
              <div class="cell-item">
                姓名
              </div>
            </template>
            {{selectedUser.name}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template #label>
              <div class="cell-item">
                最大可借阅数
              </div>
            </template>
            {{selectedUser.maxborrowing}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template #label>
              <div class="cell-item">
                已借阅
              </div>
            </template>
            {{selectedUser.currentborrowing}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template #label>
              <div class="cell-item">
                超时
              </div>
            </template>
            {{selectedUser.overtimeborrowing}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template #label>
              <div class="cell-item">
                挂失中图书
              </div>
            </template>
            {{selectedUser.loss}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template #label>
              <div class="cell-item">
                是否允许借书
              </div>
            </template>
            <el-tag type="success" v-if="selectedUser.allowBorrow == true">允许借书</el-tag>
            <el-tag type="danger" v-if="selectedUser.allowBorrow == false">禁止借书</el-tag>
          </el-descriptions-item>
        </el-descriptions>
        <div>
          <el-input
            v-model="userSearchData.searchInput"
            placeholder="请输入信息"
            style="width: 50%; margin:5px;"
          >
            <template #prepend>
              <el-select v-model="userSearchData.value" placeholder="Select" style="width: 100px">
                <el-option 
                  v-for="item in userSearchData.options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                />
              </el-select>
            </template>
            <template #append>
              <el-button @click="userSearch">搜索</el-button>
            </template>
          </el-input>
        </div>
        <el-table :data="userTableData" stripe border style="width: 100%" highlight-current-row @current-change="handleCurrentChange">
          <el-table-column prop="id" label="id" sortable/>
          <el-table-column prop="name" label="姓名" sortable/>
          <el-table-column prop="username" label="用户名" sortable/>
        </el-table>
        <div>
          <el-input
            v-model="normalSearchData.searchInput"
            placeholder="请输入条码号"
            style="width: 50%; margin:5px;"
          >
            <template #append>
              <el-button @click="search">搜索</el-button>
            </template>
          </el-input>
          <el-button @click="borrow" >借阅</el-button>
        </div>
<!-- 展示框 -->
        <el-table :data="tableData" stripe border style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55" :selectable="selectable" />
          <el-table-column prop="barcode" label="条码号" sortable/>
          <el-table-column prop="bookid" label="图书id" sortable/>
          <el-table-column prop="bookname" label="图书名" sortable/>
          <el-table-column label="状态" width="200">
            <template #default="scope">
              <span v-if="scope.row.state == 1"><font color="green">在架</font></span>
              <span v-if="scope.row.state == 0"><font color="red">借出</font></span>              
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template #default="scope">
              <el-button @click="deleteBook(scope.$index)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
<!-- 分页 -->
      </div>
</template>

<script>

import Header from "@/components/Header";
import Aside from "@/components/Aside";
import axios from "axios";
import qs from "qs";
export default {
  name: 'HomeView',
  components: {
    Header,
    Aside
  },
  data(){
    return{
      selectedUser:{id:undefined},
      userSearchData:{
        value:"学生姓名",
        searchInput:'',
        options:[
          {
            value: "学生姓名",
            label: '学生姓名',
          },
          {
            value: '学生id',
            label: '学生id',
          },
        ]
      },
      normalSearchData:{
        value:"条码号",
        searchInput:''
      },
      userTableData:[],
      tableData:[], 
      multipleSelection:[]
    }
  },
  methods:{
    deleteBook(index){
      this.tableData.splice(index, 1);
    },
    borrow(){
      if(this.selectedUser.id != undefined && this.selectedUser.allowBorrow == true){
        let barcodeList = [];
        this.multipleSelection.forEach(element=>{
          barcodeList.push(element.barcode);
        })
        axios.post("https://localhost/borrowHistory/borrowBooks",qs.stringify({barcodeList:barcodeList,length:barcodeList.length,studentid:this.selectedUser.id})).then(res=>{
          let successList = res.data;
          this.$message({
            type:"success",
            message:"成功"+successList.length+"条"
          });
          this.tableData.forEach(element=>{
            successList.forEach(successElement=>{
              if(element.barcode == successElement)
                element.state = 0;
            })
          });
          this.selectedUser.currentborrowing += successList.length;
        })
      }
      else if(this.selectedUser.id == undefined){
        this.$message({
              type:"error",
              message:"未选择借阅人"
            });
      }
      else if(this.selectedUser.allowBorrow == false){
        this.$message({
              type:"error",
              message:"该用户当前不能借阅书籍"
            });
      }
    },
    handleCurrentChange(currentRow){
      if(currentRow != null){
        this.selectedUser = currentRow;
        axios.post('https://localhost/borrowHistory/getCurrentBorrowingByUserid',qs.stringify({userid:this.selectedUser.id})).then(res=>{
          this.selectedUser["currentborrowing"] = 0;
          this.selectedUser["overtimeborrowing"] = 0;
          this.selectedUser["loss"] = 0;
          let borrowHistories = [];
          borrowHistories = res.data;
          borrowHistories.forEach(element => {
            let now = new Date();
            let duedate = new Date(element.duedate);
            this.selectedUser.currentborrowing++;
            if(Date.parse(now) > Date.parse(duedate)){
              this.selectedUser.overtimeborrowing++;
            }
            if(element.loss == 1){
              this.selectedUser.loss++;
            }
          });
          if(this.selectedUser.loss == 0 && this.selectedUser.overtimeborrowing == 0 && this.selectedUser.currentborrowing < this.selectedUser.maxborrowing){
            this.selectedUser["allowBorrow"] = true;
          }
          else{
            this.selectedUser["allowBorrow"] = false;
          }
        })
      }
    },
    handleSelectionChange(val){
      this.multipleSelection = val;
    },
    selectable(row){
      if(row.state == 0)
        return false;
      else if(row.state == 1)
        return true;
    },
    userSearch(){
      axios.post("https://localhost/student/getAll").then(res=>{
        this.userTableData = res.data;
        if(this.userSearchData.value == "学生姓名"){
          this.userTableData = this.userTableData.filter(element=>{
            if(element.name == this.userSearchData.searchInput)
              return true;
          })
        }
        else if(this.userSearchData.value == "学生id"){
          this.userTableData = this.userTableData.filter(element=>{
            if(element.id == this.userSearchData.searchInput)
              return true;
          })
        }
      })
    },
    search(){
      if(this.normalSearchData.searchInput != ''){
        axios.post("https://localhost/holdingInformation/getBookByBarcode",qs.stringify({barcode:this.normalSearchData.searchInput})).then(res=>{
          let flag = true;
          this.tableData.forEach(element=>{
            if(element.barcode == res.data.barcode)
              flag = false;
          })
          if(flag == true)
            this.tableData.push(res.data);
          this.tableData.forEach(element=>{
            axios.post("https://localhost/book/getBookById",qs.stringify({bookid:element.bookid})).then(res=>{
              element["bookname"] = res.data.bookname;
            })
          })
        })
      }
    }
  },
  created(){
  }
}
</script>
