<template>
  <div style="height: 50px;line-height: 50px;border-bottom: 1px solid skyblue;display: flex">
    <div style="width: 200px;font-weight: bolder">欢迎您，管理员{{username}}</div>
    <div style="flex: 1"></div>
    
    <div style="width: 100px">
        <el-dropdown>
          <el-button type="primary">
            {{username}}<el-icon class="el-icon--right"><arrow-down /></el-icon>
          </el-button>
          <template #dropdown>
            <el-dropdown-menu>
              <el-dropdown-item @click="OpenPersonalInfo">个人信息</el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
    </div>
  </div>
</template>

<script>
import DropDown from "@/components/DropDown";
import axios from 'axios';
import qs from 'qs'
export default {
  name: "Header",
  components:{
    DropDown
  },
  data() {
    return {
      username:undefined, 
    }
  },
  methods:{
    OpenPersonalInfo(){
      axios.post('https://localhost/server/url').then(res=>{
        window.open(res.data+"/personalCenter");
      })
    }
  },
  created(){
    let token = JSON.parse(localStorage.getItem('token'));
    let id = token.id;
    axios.post('https://localhost/user/getUser',qs.stringify({id:id})).then(res=>{
      this.username = res.data.username;
    })
  }
}
</script>

