<template>
      <div style="width: 100%;height: 100%">
        <div style="margin: 5px">
          <el-button type="primary" @click="newBookDialogData.dialogVisible = true;">新增图书</el-button>
        </div>

        <div>
          <el-input
            v-model="normalSearchData.searchInput"
            placeholder="请输入信息"
            style="width: 50%; margin:5px;"
          >
            <template #prepend>
              <el-select v-model="normalSearchData.value" placeholder="Select" style="width: 100px">
                <el-option 
                  v-for="item in normalSearchData.options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                />
              </el-select>
            </template>
            <template #append>
              <el-button @click="search">搜索</el-button>
            </template>
          </el-input>
          <el-button @click="reloadForm()" >刷新表单</el-button>
        </div>
<!-- 展示框 -->
        <el-table height="500" :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)" stripe border style="width: 100%">
          <el-table-column prop="bookid" label="图书id" sortable/>
          <el-table-column prop="bookname" label="图书名"/>
          <el-table-column prop="author" label="作者" />
          <el-table-column prop="press" label="出版社" />
          <el-table-column prop="holdingnum" label="馆藏数量" />
          <el-table-column label="操作">
            <template #default="scope">
              <el-popconfirm @confirm="deleteConfirm" title="确认删除吗？">
                <template #reference>
                  <el-button size="mini" type="danger" @click="deleteBook(scope.row.bookid)">删除</el-button>
                </template>
              </el-popconfirm>
              <el-button size="mini" @click="openEditDialog(scope.row)">查看图书信息</el-button>
            </template>
          </el-table-column>
        </el-table>
<!-- 分页 -->
        <div>
          <el-pagination
              v-model:current-page="currentPage"
              :page-sizes="[5, 10, 20]"
              v-model:page-size="pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange">
          </el-pagination>
        </div>
        <el-dialog v-model="newBookDialogData.dialogVisible" title="新增书籍" >
          <el-form label-width="auto" :label-position="right"  ref="form" :rules="rules" :model="newBookDialogData.bookInfo">
            <el-form-item label="图书名" prop="bookname">
              <el-input  placeholder="请输入内容" v-model="newBookDialogData.bookInfo.bookname"/>
            </el-form-item>
            <el-form-item label="作者" prop="author">
              <el-input  placeholder="请输入内容" v-model="newBookDialogData.bookInfo.author"/>
            </el-form-item>
            <el-form-item label="出版社" prop="press">
              <el-input  placeholder="请输入内容" v-model="newBookDialogData.bookInfo.press"/>
            </el-form-item>
            <el-form-item label="类型" prop="category">
              <el-input  placeholder="请输入内容" v-model="newBookDialogData.bookInfo.category"/>
            </el-form-item>
            <el-form-item label="语言" prop="language">
              <el-input  placeholder="请输入内容" v-model="newBookDialogData.bookInfo.language"/>
            </el-form-item>
            <el-form-item label="出版日期" prop="publicationdate">
              <el-date-picker
                v-model="newBookDialogData.bookInfo.publicationdate"
                type="year"
                placeholder="选择出版日期"
              />
            </el-form-item>
                <el-button type="primary" @click="newBookDialogData.dialogVisible = false;">取消</el-button>
                <el-button type="primary" @click="confirmAdd">确认</el-button>
          </el-form>
        </el-dialog>
        <el-dialog v-model="editDialogData.dialogVisible" title="图书信息">
          <el-descriptions
            class="margin-top"
            title="用户信息"
            :column="3"
            :size="size"
            border
          >
            <el-descriptions-item>
              <template #label>
                <div class="cell-item">
                  图书id
                </div>
              </template>
              <span>{{editDialogData.bookInfo.bookid}}</span>
            </el-descriptions-item>
            <el-descriptions-item>
              <template #label>
                <div class="cell-item">
                  图书名
                </div>
              </template>
              <span v-if="editDialogData.bookInfoEditable == false">{{editDialogData.bookInfo.bookname}}</span>
              <el-input type="text" v-model="editDialogData.bookInfo.bookname" v-if="editDialogData.bookInfoEditable == true"/>
            </el-descriptions-item>
            <el-descriptions-item>
              <template #label>
                <div class="cell-item">
                  作者
                </div>
              </template>
              <span v-if="editDialogData.bookInfoEditable == false">{{editDialogData.bookInfo.author}}</span>
              <el-input type="text" v-model="editDialogData.bookInfo.author" v-if="editDialogData.bookInfoEditable == true"/>
            </el-descriptions-item>
            <el-descriptions-item>
              <template #label>
                <div class="cell-item">
                  出版社
                </div>
              </template>
              <span v-if="editDialogData.bookInfoEditable == false">{{editDialogData.bookInfo.press}}</span>
              <el-input type="text" v-model="editDialogData.bookInfo.press" v-if="editDialogData.bookInfoEditable == true"/>
            </el-descriptions-item>
            <el-descriptions-item>
              <template #label>
                <div class="cell-item">
                  出版日期
                </div>
              </template>
              <span v-if="editDialogData.bookInfoEditable == false">{{editDialogData.bookInfo.publicationdate.getFullYear()}}</span>
              <el-date-picker
                v-model="editDialogData.bookInfo.publicationdate"
                type="year"
                placeholder="选择出版日期"
                v-if="editDialogData.bookInfoEditable == true"
              />
            </el-descriptions-item>
            <el-descriptions-item>
              <template #label>
                <div class="cell-item">
                  类型
                </div>
              </template>
              <span v-if="editDialogData.bookInfoEditable == false">{{editDialogData.bookInfo.category}}</span>
              <el-input v-model="editDialogData.bookInfo.category" v-if="editDialogData.bookInfoEditable == true"/>
            </el-descriptions-item>
            <el-descriptions-item>
              <template #label>
                <div class="cell-item">
                  语言
                </div>
              </template>
              <span v-if="editDialogData.bookInfoEditable == false">{{editDialogData.bookInfo.language}}</span>
              <el-input v-model="editDialogData.bookInfo.language" v-if="editDialogData.bookInfoEditable == true"/>
            </el-descriptions-item>
          </el-descriptions>
          <el-button v-if="editDialogData.bookInfoEditable == false" @click="editDialogData.bookInfoEditable = true">编辑</el-button>
          <el-button v-if="editDialogData.bookInfoEditable == true" @click="editBookInfo">保存</el-button>
          <el-table :data="editDialogData.holdingInfo" stripe border>
            <el-table-column prop="barcode" label="条码号">
              <template #default="scope">
                  <el-input v-model="scope.row.newBarcode" v-show="scope.row.editable" placeholder="请输入内容"></el-input>
                  <span v-show="!scope.row.editable">{{scope.row.barcode}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="position" label="馆藏地">
              <template #default="scope">
                  <el-input v-model="scope.row.position" v-show="scope.row.editable" placeholder="请输入内容"></el-input>
                  <span v-show="!scope.row.editable">{{scope.row.position}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="booknum" label="索书码" >
              <template #default="scope">
                  <el-input v-model="scope.row.booknum" v-show="scope.row.editable" placeholder="请输入内容"></el-input>
                  <span v-show="!scope.row.editable">{{scope.row.booknum}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="state" label="状态">
              <template #default="scope">
                  <el-radio v-if="scope.row.editable" v-model="scope.row.state" label="0" size="large">借出</el-radio>
                  <el-radio v-if="scope.row.editable" v-model="scope.row.state" label="1" size="large">在架</el-radio>
                  <span v-show="!scope.row.editable && scope.row.state == 1">在架</span>
                  <span v-show="!scope.row.editable && scope.row.state == 0">借出</span>
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template #default="scope">
                <el-button type="primary" v-if="scope.row.editable == false" @click="scope.row.editable = true">编辑</el-button>
                <el-button type="primary" v-if="scope.row.editable == true" @click="editHoldingInfo(scope.row)">保存</el-button>
                <el-popconfirm @confirm="deleteConfirm" title="确认删除吗？">
                  <template #reference>
                    <el-button size="mini" type="danger">删除</el-button>
                  </template>
                </el-popconfirm>
              </template>
            </el-table-column>
          </el-table>
          <el-button @click="addNewHoldingInfo">添加馆藏信息</el-button>
        </el-dialog>
      </div>
</template>

<script>
import Header from "@/components/Header";
import Aside from "@/components/Aside";
import axios from "axios";
import qs from "qs";
export default {
  name: 'HomeView',
  components: {
    Header,
    Aside
  },
  data(){
    return{
      rules: {
        bookname: [
          { required: true, message: '不能为空', trigger: 'blur' },
        ],
      },
      tableData:[],
      operationid:undefined,
      currentPage:1,
      total: 20, 
      pageSize: 20, 
      editDialogData:{
        dialogVisible:false,
        bookInfoEditable:false,
        holdingInfoEditable:false,
        bookInfo:{},
        holdingInfo:[],
      },
      newBookDialogData:{
        dialogVisible:false,
        bookInfo:{},
      },
      normalSearchData:{
        value:"图书名",
        searchInput:'',
        options:[
          {
            value: '图书名',
            label: '图书名',
          },
          {
            value: "图书id",
            label: '图书id',
          },
          {
            value: '作者',
            label: '作者',
          },
          {
            value: '出版社',
            label: '出版社',
          },
        ]
      },
    }
  },
  methods:{
    addNewHoldingInfo(){
      this.editDialogData.holdingInfo.push({barcode:undefined,bookid:this.editDialogData.bookInfo.bookid,position:undefined,booknum:undefined,state:1,editable:true,newBarcode:''})
    },
    editBookInfo(){
      axios.post("https://localhost/book/edit",qs.stringify(this.editDialogData.bookInfo)).then(res=>{
        if(res.data == true){
          this.$message({
            type:"success",
            message:"修改成功"
          })
          this.editDialogData.bookInfoEditable = false;
        }
      })
    },
    editHoldingInfo(row){
      axios.post("https://localhost/holdingInformation/editHoldingInfo",qs.stringify(row)).then(res=>{
        if(res.status == 202){
          this.$message({
            type:"error",
            message:"条码号已存在"
          })
        }
        else if(res.status == 201){
          this.$message({
            type:"error",
            message:"条码号不能为空"
          })
        }
        else if(res.status == 200){
          this.$message({
            type:"success",
            message:"修改成功"
          })
          axios.post("https://localhost/holdingInformation/getHoldingInformationByBookid",qs.stringify({bookid:row.bookid})).then(res=>{
            this.editDialogData.holdingInfo = res.data;
            this.editDialogData.holdingInfo.forEach(element=>{
              element["editable"] = false;
              element["newBarcode"] = element.barcode
            })
          })
        }
      })
    },
    openEditDialog(row){
      this.editDialogData.dialogVisible = true;
      this.editDialogData.bookInfo = row;
      axios.post("https://localhost/holdingInformation/getHoldingInformationByBookid",qs.stringify({bookid:row.bookid})).then(res=>{
        this.editDialogData.holdingInfo = res.data;
        this.editDialogData.holdingInfo.forEach(element=>{
          element["editable"] = false;
          element["newBarcode"] = element.barcode
        })
      })
    },
    reloadForm(){
    },
    confirmAdd(){
      this.$refs.form.validate(valid => {
        if (valid) {
          axios.post('https://localhost/book/add',qs.stringify(this.newBookDialogData.bookInfo)).then(res=>{
              if(res.data == true){
                this.dialogVisible = false;
                this.$message({
                  type:"success",
                  message:"添加成功"
                })
              }
              else{
                this.$message({
                  type:"error",
                  message:"添加失败!"
                })
              }
          });
        } 
        else
          alert("输入有误！")
      }) 
    },
    deleteBook(id){
      this.operationid = id;
    },
    deleteConfirm(){
      var deleteid_token = JSON.parse(localStorage.getItem('token'));
      deleteid_token["deleteid"] = this.operationid;
      axios.post('https://localhost/book/delete',qs.stringify(deleteid_token)).then(res=>{
        if(res.status == 200)
          this.$message({
            type:"success",
            message:"删除成功"
          });
        else if(res.status == 201)
          alert("删除失败！");
        this.tableData = res.data;
      });
    },
    search(){
			 axios.post('https://localhost/book',qs.stringify(JSON.parse(localStorage.getItem('token')))).then(res=>{
        this.tableData = res.data;
        if(this.normalSearchData.searchInput != ''){
          this.tableData = this.tableData.filter(element=>{
            if(this.normalSearchData.value == "图书名"){
              if(element.bookname.match(this.normalSearchData.searchInput) != null){
                return true
              }
            }
            if(this.normalSearchData.value == "图书id"){
              if(element.bookid == this.normalSearchData.searchInput){
                return true
              }
            }
            if(this.normalSearchData.value == "作者"){
              if(element.author.match(this.normalSearchData.searchInput) != null){
                return true
              }
            }
            if(this.normalSearchData.value == "出版社"){
              if(element.press.match(this.normalSearchData.searchInput) != null){
                return true
              }
            }
          })
        }
        this.tableData.forEach(element=>{
          element.publicationdate = new Date(element.publicationdate);
          axios.post("https://localhost/holdingInformation/getHoldingnumByBookid",qs.stringify({bookid:element.bookid})).then(res=>{
            element["holdingnum"] = res.data;
          })
        })
      })
    }
  },
  created(){
    this.search();
  }
}
</script>
