<template>
      <div style="width: 100%;height: 100%">
        <div>
          <div>
            <el-button type="primary" @click="newUserDialogData.dialogVisible = true;">新增用户</el-button>
          </div>
          <el-input
            v-model="normalSearchData.searchInput"
            placeholder="请输入信息"
            style="width: 50%; margin:5px;"
          >
            <template #prepend>
              <el-select v-model="normalSearchData.value" placeholder="Select" style="width: 100px">
                <el-option 
                  v-for="item in normalSearchData.options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                />
              </el-select>
            </template>
            <template #append>
              <el-button @click="search">搜索</el-button>
            </template>
          </el-input>
        </div>
<!-- 展示框 -->
        <el-table :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)" stripe border style="width: 100%">
          <el-table-column prop="id" label="用户id" sortable/>
          <el-table-column prop="username" label="用户名"/>
          <el-table-column prop="name" label="真实姓名"/>
          <el-table-column prop="maxborrowing" label="可借阅书籍"/>
          <el-table-column prop="currentborrowing" label="已借阅书籍" />
          <el-table-column prop="overtimeborrowing" label="超时书籍" />
          <el-table-column prop="loss" label="挂失书籍" />
          <el-table-column label="状态">
            <template #default="scope">
              <span v-if="scope.row.allowBorrow == true"><font color="green">允许借书</font></span>
              <span v-if="scope.row.allowBorrow == false"><font color="red">禁止借书</font></span>
            </template>    
          </el-table-column>
          <el-table-column label="操作">
            <template #default="scope">
              <el-popconfirm @confirm.stop="deleteConfirm" title="确认注销用户吗？该操作无法撤销！">
                <template #reference>
                  <el-button size="mini" type="danger" @click.stop="deleteStudent(scope.row.id)">注销</el-button>
                </template>
              </el-popconfirm>
              <el-button  size="mini" @click.stop="borrowHistory(scope.row)">查询借阅记录</el-button>
            </template>
          </el-table-column>
        </el-table>
<!-- 分页 -->
        <div>
          <el-pagination
              v-model:current-page="currentPage"
              :page-sizes="[5, 10, 20]"
              v-model:page-size="pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
          </el-pagination>
        </div>
        <el-dialog v-model="newUserDialogData.dialogVisible" title="新增用户" >
          <el-form label-width="auto" :label-position="right"  ref="form" :rules="rules" :model="newUserDialogData.userInfo">
            <el-form-item label="id" prop="id">
              <el-input  placeholder="请输入正整数" v-model="newUserDialogData.userInfo.id"/>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input  placeholder="请输入内容" v-model="newUserDialogData.userInfo.password"/>
            </el-form-item>
            <el-form-item label="姓名" prop="name">
              <el-input  placeholder="请输入内容" v-model="newUserDialogData.userInfo.name"/>
            </el-form-item>
            <el-form-item label="用户名" prop="username">
              <el-input  placeholder="请输入内容" v-model="newUserDialogData.userInfo.username"/>
            </el-form-item>
                <el-button type="primary" @click="newUserDialogData.dialogVisible = false;">取消</el-button>
                <el-button type="primary" @click="confirmAdd">确认</el-button>
          </el-form>
        </el-dialog>
      </div>
</template>

<script>

import Header from "@/components/Header";
import Aside from "@/components/Aside";
import axios from "axios";
import qs from "qs";
import router from '@/router'
import emitter from "../utils/eventbus.js"
export default {
  name: 'HomeView',
  components: {
    Header,
    Aside
  },
  data(){
    return{
      tableData:[],
      operationid:undefined,
      currentPage:1,
      total: 20, 
      pageSize: 20, 
      normalSearchData:{
        value:"学生姓名",
        searchInput:'',
        options:[
          {
            value: '学生姓名',
            label: '学生姓名',
          },
          {
            value: "用户名",
            label: '用户名',
          },
          {
            value: 'id',
            label: 'id',
          },
        ]
      },
      newUserDialogData:{
        dialogVisible:false,
        userInfo:{},
      },
      rules: {
        password: [
          { required: true, message: '不能为空', trigger: 'blur' },
        ],
        id:[{ required: true, message: '不能为空', trigger: 'blur' },
                { validator(rule, value, callback) {
                      if (Number.isInteger(Number(value)) && Number(value) > 0)
                        callback()
                      else
                        callback(new Error('请输入正整数'))
                      },trigger:"blur"}
        ],
      },
    }
  },
  methods:{
    borrowHistory(row){
      router.push("/borrowHistory");
      emitter.on("borrowtostudent",data=>{
        emitter.emit("studenttoborrow",row)
        emitter.off("borrowtostudent")
      })
    },
    confirmAdd(){
      this.$refs.form.validate(valid => {
        if (valid) {
          axios.post('https://localhost/student/register',qs.stringify(this.newUserDialogData.userInfo)).then(res=>{
              if(res.status == 200){
                this.dialogVisible = false;
                this.$message({
                  type:"success",
                  message:"添加成功"
                })
              }
              else if(res.status == 201){
                this.$message({
                  type:"error",
                  message:"id已存在"
                })
              }
          });
        } 
        else
          alert("输入有误！")
      }) 
    },
    deleteStudent(id){
      this.operationid = id;
    },
    deleteConfirm(){
      var deleteid_token = JSON.parse(localStorage.getItem('token'));
      deleteid_token["deleteid"] = this.operationid;
      axios.post('https://localhost/student/delete',qs.stringify(deleteid_token)).then(res=>{
        if(res.status == 200)
          this.$message({
            type:"success",
            message:"删除成功"
          });
        else if(res.status == 201)
          this.$message({
            type:"error",
            message:"删除失败!"
          });
        this.tableData = res.data;
      });
    },
    search(){
      axios.post('https://localhost/student/getAll',qs.stringify(JSON.parse(localStorage.getItem('token')))).then(res=>{
        this.tableData = res.data;
        if(this.normalSearchData.searchInput != ''){
          this.tableData = this.tableData.filter(data=>{
            if(this.normalSearchData.value == "学生姓名"){
              if(data.name.match(this.normalSearchData.searchInput) != null)
                return true;
            }
            if(this.normalSearchData.value == "用户名"){
              if(data.username.match(this.normalSearchData.searchInput) != null)
                return true;         
            }
            if(this.normalSearchData.value == "id"){
              if(data.id == this.normalSearchData.searchInput)
                return true;
            }
          })
        }
        this.tableData.forEach(user=>{
          axios.post('https://localhost/borrowHistory/getCurrentBorrowingByUserid',qs.stringify({userid:user.id})).then(res=>{
            user["currentborrowing"] = 0;
            user["overtimeborrowing"] = 0;
            user["loss"] = 0;
            let borrowHistories = [];
            borrowHistories = res.data;
            borrowHistories.forEach(element => {
              let now = new Date();
              let duedate = new Date(element.duedate);
              user.currentborrowing++;
              if(Date.parse(now) > Date.parse(duedate)){
                user.overtimeborrowing++;
              }
              if(element.loss == 1){
                user.loss++;
              }
            });
            if(user.loss == 0 && user.overtimeborrowing == 0 && user.currentborrowing < user.maxborrowing){
              user["allowBorrow"] = true;
            }
            else{
              user["allowBorrow"] = false;
            }
          })
        })
      })	 
    }
  },
  created(){
    this.search();
  },
}
</script>
