<template>
  <div style="width: 100%;height: 100%">
    <div>
      <el-input
        v-model="normalSearchData.searchInput"
        placeholder="请输入信息"
        style="width: 50%; margin:5px;"
      >
        <template #prepend>
          <el-select v-model="normalSearchData.value" placeholder="Select" style="width: 100px">
            <el-option 
              v-for="item in normalSearchData.options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </template>
        <template #append>
          <el-button @click="search">搜索</el-button>
        </template>
      </el-input>
      <el-button @click="reloadForm()" >刷新表单</el-button>
    </div>
<!-- 展示框 -->
    <el-tabs v-model="tab" class="demo-tabs" @tab-click="switchVisibleBook">
      <el-tab-pane label="所有记录" name="all"></el-tab-pane>
      <el-tab-pane label="未归还" name="notReturn"></el-tab-pane>
      <el-tab-pane label="已完成" name="finished"></el-tab-pane>
      <el-tab-pane label="超时" name="overtime"></el-tab-pane>
      <el-tab-pane label="挂失" name="loss"></el-tab-pane>
    </el-tabs>
    <el-table height="500" :data="visibleTableData.slice((currentPage-1)*pageSize,currentPage*pageSize)" stripe border style="width: 100%">
      <el-table-column prop="borrowid" label="记录id" sortable/>
      <el-table-column prop="bookname" label="图书名" sortable/>
      <el-table-column prop="barcode" label="条码号" sortable/>
      <el-table-column prop="name" label="借阅学生名" sortable/>
      <el-table-column label="借阅时间" width="200">
        <template #default="scope">
          <span>{{scope.row.borrowtime.getFullYear()}}-{{scope.row.borrowtime.getMonth() + 1}}-{{scope.row.borrowtime.getDate()}}</span>
        </template>
      </el-table-column>
      <el-table-column label="应还时间" width="200">
        <template #default="scope">
          <span>{{scope.row.duedate.getFullYear()}}-{{scope.row.duedate.getMonth() + 1}}-{{scope.row.duedate.getDate()}}</span>
        </template>
      </el-table-column>
      <el-table-column label="还书时间" width="200">
        <template #default="scope">
          <span v-if="scope.row.returntime != null">{{scope.row.returntime.getFullYear()}}-{{scope.row.returntime.getMonth() + 1}}-{{scope.row.returntime.getDate()}}</span>
          <span v-if="scope.row.returntime == null && scope.row.overtime && scope.row.loss == 0"><font color="orange">超时</font></span>
          <span v-if="scope.row.returntime == null && !scope.row.overtime && scope.row.loss == 0"><font color="green">未还书</font></span>
          <span v-if="scope.row.loss == 1"><font color="red">挂失未缴费</font></span>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="200">
        <template #default="scope">
          <el-popconfirm v-if="scope.row.returntime == null && scope.row.loss == 0" @confirm.stop="returnBook(scope.row)" title="确认手动还书吗？该操作无法撤销">
            <template #reference>
              <el-button size="mini">手动还书</el-button>
            </template>
          </el-popconfirm>
          <el-popconfirm v-if="scope.row.loss == 1" @confirm.stop="pay(scope.row)" title="确认用户已缴费吗？该操作无法撤销">
            <template #reference>
              <el-button size="mini">手动缴费</el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
<!-- 分页 -->
    <div>
      <el-pagination
          v-model:current-page="currentPage"
          :page-sizes="[5, 10, 20]"
          v-model:page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>

import Header from "@/components/Header";
import Aside from "@/components/Aside";
import axios from "axios";
import qs from "qs";
import bus from "vue3-eventbus"
import emitter from "../utils/eventbus.js"
export default {
  name: 'HomeView',
  components: {
    Header,
    Aside
  },
  data(){
    return{
      tableData:[],
      operationid:undefined,
      visibleTableData:[],
      tab:"all",
      currentPage:1,
      total: 20, 
      pageSize: 20, 
      normalSearchData:{
        value:"学生姓名",
        searchInput:'',
        options:[
          {
            value: "学生姓名",
            label: '学生姓名',
          },
          {
            value: '学生id',
            label: '学生id',
          },
          {
            value: '记录id',
            label: '记录id',
          },
        ]
      },
    }
  },
  methods:{
    pay(row){
      axios.post("https://localhost/borrowHistory/pay",qs.stringify({borrowid:row.borrowid})).then(res=>{
        if(res.data == true)
          this.search();
      })
    },
    returnBook(row){
      axios.post("https://localhost/borrowHistory/returnBook",qs.stringify({borrowid:row.borrowid,barcode:row.barcode})).then(res=>{
        if(res.data == true)
          this.search();
      })
    },
    switchVisibleBook(tab){
      this.tab = tab.props.name;
      if(this.tab == "all")
        this.visibleTableData = this.tableData;
      else if(this.tab == "notReturn")
        this.visibleTableData = this.tableData.filter(data=>{
          if(data.returntime == null && !data.overtime && data.loss == 0)
            return true;
        })
      else if(this.tab == "finished")
        this.visibleTableData = this.tableData.filter(data=>{
          if(data.returntime != null)
            return true;
        })
      else if(this.tab == "overtime")
        this.visibleTableData = this.tableData.filter(data=>{
          if(data.returntime == null && data.overtime && data.loss == 0)
            return true;
        })
      else if(this.tab == "loss")
        this.visibleTableData = this.tableData.filter(data=>{
          if(data.loss == 1)
            return true;
        })
    },
    reloadForm(){
      axios.post('https://localhost/borrowHistory/getAll',qs.stringify(JSON.parse(localStorage.getItem('token')))).then(res=>{
      this.tableData = res.data;
      })
    },
    async search(){
      await axios.post('https://localhost/borrowHistory/getAll').then(async res=>{
        this.tableData = res.data;
        for(let i = 0;this.tableData[i] != null;i++)
        {
            let element = this.tableData[i];
            element.borrowtime = new Date(element.borrowtime);
            element.duedate = new Date(element.duedate);
            if(element.returntime != null){
              element.returntime = new Date(element.returntime);
            }
            let now = Date.parse(Date());
            if(now < Date.parse(element.duedate)){
              element["overtime"] = false;
            }
            else{
              element["overtime"] = true;
            }
            await axios.post("https://localhost/borrowHistory/getBookidByBarcode",qs.stringify({barcode:element.barcode})).then(res=>{
              element["bookid"] = res.data;
              axios.post("https://localhost/book/getBookById",qs.stringify({bookid:element.bookid})).then(res=>{
                element["bookname"] = res.data.bookname;
              })
            })
            await axios.post("https://localhost/student/getStudent",qs.stringify({id:element.studentid})).then(res=>{
              element["name"] = res.data.name
            })
            element["show"] = false;
        };
      })
      if(this.normalSearchData.searchInput != ""){
        this.tableData = this.tableData.filter(element=>{
          if(this.normalSearchData.value == "学生姓名"){
            if(element.name == this.normalSearchData.searchInput){
              return true;
            }
          }
          else if(this.normalSearchData.value == "学生id"){
            if(element.studentid == this.normalSearchData.searchInput){
              return true;
            }
          }
          else if(this.normalSearchData.value == "记录id"){
            if(element.borrowid == this.normalSearchData.searchInput){
              return true;
            }
          }
        })
      }
      if(this.tab == "all")
        this.visibleTableData = this.tableData;
      else if(this.tab == "notReturn")
        this.visibleTableData = this.tableData.filter(data=>{
          if(data.returntime == null && !data.overtime && data.loss == 0)
            return true;
        })
      else if(this.tab == "finished")
        this.visibleTableData = this.tableData.filter(data=>{
          if(data.returntime != null)
            return true;
        })
      else if(this.tab == "overtime")
        this.visibleTableData = this.tableData.filter(data=>{
          if(data.returntime == null && data.overtime && data.loss == 0)
            return true;
        })
      else if(this.tab == "loss")
        this.visibleTableData = this.tableData.filter(data=>{
          if(data.loss == 1)
            return true;
        })
    }
  },
  created(){
    emitter.on("studenttoborrow",data=>{
      this.normalSearchData.searchInput = data.id;
      this.normalSearchData.value = "学生id"
      this.tab = "all"
    })
    emitter.emit("borrowtostudent","hello")
    emitter.off("studenttoborrow");
    this.search();
  },
}
</script>
